      top: 4.5rem;
      right: 4.5rem; } }
  @media (max-width: 600px) {
    .navigation__background {
      top: 3.5rem;
      right: 3.5rem; } }
  .navigation__background-shown {
    -webkit-transform: scale(80);
            transform: scale(80); }

.navigation__nav {
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1500;
  opacity: 0;
  width: 0;
  -webkit-transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55);
  transition: all 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55); }
  .navigation__nav-shown {
    opacity: 1;
    width: 100%; }

.navigation__list {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
