  font-size: 3rem;
  font-weight: 300;
  color: #000;
  text-decoration: none;
  text-transform: uppercase;
  padding: 1rem 2rem;
  letter-spacing: 0.2rem;
  position: relative;
  -webkit-transition: all 0.4s;
  transition: all 0.4s; }

.navigation__link:hover, .navigation__link:active {
  color: #ff7730; }

